<template>
	<Example vertical id="example-layout-header-sider-second">
		<template slot="source">
			<Browser>
				<vui-layout>
					<vui-header theme="dark">
						<h1 style="float: left; width: 124px; height: 30px; background-color: rgba(255,255,255,0.2); margin: 17px 0;"></h1>
						<vui-menu theme="dark" mode="horizontal" selectedName="1" style="float: left; margin-left: 25px; line-height: 64px;">
							<vui-menu-item name="1">Nav 1</vui-menu-item>
							<vui-menu-item name="2">Nav 2</vui-menu-item>
							<vui-menu-item name="3">Nav 3</vui-menu-item>
						</vui-menu>
					</vui-header>
					<vui-layout>
						<vui-sider width="200">
							<vui-menu mode="inline" :openNames="['1']" :selectedName="'1-1'" style="min-height: 100%; box-shadow: none;">
								<vui-submenu name="1">
									<template slot="title">
										<vui-icon type="apps" />
										<span>SubNav 1</span>
									</template>
									<vui-menu-item name="1-1">Nav 1-1</vui-menu-item>
									<vui-menu-item name="1-2">Nav 1-2</vui-menu-item>
									<vui-menu-item name="1-3">Nav 1-3</vui-menu-item>
									<vui-menu-item name="1-4">Nav 1-4</vui-menu-item>
								</vui-submenu>
								<vui-submenu name="2">
									<template slot="title">
										<vui-icon type="mail" />
										<span>SubNav 2</span>
									</template>
									<vui-menu-item name="2-1">Nav 2-1</vui-menu-item>
									<vui-menu-item name="2-2">Nav 2-2</vui-menu-item>
									<vui-menu-item name="2-3">Nav 2-3</vui-menu-item>
									<vui-menu-item name="2-4">Nav 2-4</vui-menu-item>
								</vui-submenu>
								<vui-submenu name="3">
									<template slot="title">
										<vui-icon type="settings" />
										<span>SubNav 3</span>
									</template>
									<vui-menu-item name="3-1">Nav 3-1</vui-menu-item>
									<vui-menu-item name="3-2">Nav 3-2</vui-menu-item>
									<vui-menu-item name="3-3">Nav 3-3</vui-menu-item>
									<vui-menu-item name="3-4">Nav 3-4</vui-menu-item>
								</vui-submenu>
							</vui-menu>
						</vui-sider>
						<vui-layout style="padding: 25px;">
							<vui-breadcrumb style="margin-bottom: 25px;">
								<vui-breadcrumb-item>Home</vui-breadcrumb-item>
								<vui-breadcrumb-item>Components</vui-breadcrumb-item>
								<vui-breadcrumb-item>Layout</vui-breadcrumb-item>
							</vui-breadcrumb>
							<vui-content style="min-height: 250px; background-color: #fff; padding: 25px;">Content</vui-content>
						</vui-layout>
					</vui-layout>
				</vui-layout>
			</Browser>
		</template>
		<template slot="title">顶部 - 侧边布局</template>
		<template slot="describe">
			<p>同样拥有顶部导航及侧边栏，区别是两边未留边距，多用于应用型的网站。</p>
		</template>
		<template slot="code">{{code}}</template>
	</Example>
</template>

<script>
	import Example from "@/components/example";
	import Browser from "@/components/browser";
	import code from "./code";

	export default {
		components: {
			Example,
			Browser
		},
		data() {
			return {
				code
			};
		}
	};
</script>


